.container {
    width: 100%;
    max-width: 1250px;
    margin: auto;
}

.topic {
    width: 171px;
    height: 34px;
    -webkit-text-stroke: 1px #707070;
    font-family: CenturyGothic;
    font-size: 28px;
    font-weight: bold;
    text-align: left;
    color: #707070;
    margin: 31px 0 15px 0;
}

.page-optimization {
    display: flex;
}

.all-list-container {
    width: 100%;
    height: 100%;
    max-height: 635px;
    overflow-y: scroll;
    overflow-x: visible;
    -ms-overflow-style: none;
}

.all-list-container::-webkit-scrollbar {
    display: none;
}

.line-chart-container {
    width: 100%;
    max-width: 913px;
    border-radius: 10px;
    box-shadow: 0 3px 15px 0 rgba(0, 0, 0, 0.1);
    background-color: #fcfcfc;
    margin-top: 20px;
}

.graph-up {
    display: flex;
}
/* 排名、分數類別的大型組件 */
.page-count {
    width: 258.5px;
    height: 282px;
    padding: 12px 18.5px 18px 17px;
    border-radius: 10px;
    box-shadow: 0 3px 15px 0 rgba(0, 0, 0, 0.1);
    background-color: #fcfcfc;
}

/* 指標性分數的小型組件 */
.indicator {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-right: 20px;
}

.indicator > div {
    width: 300px;
    height: 131px;
    border-radius: 10px;
    box-shadow: 0 3px 15px 0 rgba(0, 0, 0, 0.1);
    background-color: #fcfcfc;
}
.title {
    display: flex;
    margin-bottom: 19px;
}
.title div {
    font-family: CenturyGothic;
    font-weight: bold;
    font-size: 16px;
    color: #707070;
}

.rank-icon {
    width: 23px;
    height: 23px;
    background-image: url('../../PageOptimization/static_svg/speed.svg');
    background-position: center;
    background-size: cover;
    margin-right: 16.5px;
}

.rank-content {
    position: relative;
    display: flex;
    height: 22px;
    width: 100%;
    margin-bottom: 5px;
}

.rank-num {
    width: 10px;
    height: 22px;
    font-family: CenturyGothic;
    font-size: 18px;
    margin-left: 7px;
    font-weight: bold;
    text-align: left;
}

.rank-name {
    height: 17px;
    font-family: CenturyGothic;
    font-size: 14px;
    font-weight: bold;
    text-align: left;
    margin: 0 56px 0 22.5px;
    line-height: 22px;
}

.rank-score {
    position: absolute;
    right: 0;
    height: 16px;
    font-family: CenturyGothic;
    font-size: 12px;
    text-align: left;
    color: #707070;
    margin: auto 0;
    line-height: 22px;
}
.inter {
    padding: 20px 0 20px 25px;
}
.average-icon {
    width: 83px;
    height: 83px;
    margin: 4px 20px 0 0;
    background-size: cover;
    background: url('../../PageOptimization/static_svg/growth.svg') center;
}

.compare-icon {
    width: 83px;
    height: 83px;
    margin: 4px 20px 0 0;
    background-size: cover;
    background: url('../../PageOptimization/static_svg/compare.svg') center;
}

.indicator-icon {
    width: 23px;
    height: 23px;
    background-image: url('../../PageOptimization/static_svg/indicator.svg');
    background-position: center;
    background-size: cover;
    margin-right: 16.5px;
}

.inter {
    display: flex;
}

.inter-title {
    width: 100%;
    height: 21px;
    font-family: CenturyGothic;
    font-size: 16px;
    font-weight: bold;
    text-align: left;
    color: #707070;
}

.inter-value {
    height: 34px;
    font-family: CenturyGothic;
    font-size: 28px;
    font-weight: bold;
    text-align: left;
    color: #ff7f2f;
    margin: 10px 0;
}

.inter-content {
    width: 120px;
    height: 16px;
    font-family: CenturyGothic;
    font-size: 12px;
    text-align: left;
    color: #a29d9d;
}

.trace-icon {
    width: 23px;
    height: 23px;
    background-image: url('../../PageOptimization/static_svg/trace.svg');
    background-position: center;
    background-size: cover;
    margin-right: 5px;
}

.tag-box {
    display: flex;
    flex-wrap: wrap;
}

.tag {
    width: auto;
    margin: 4px 6px 0 0;
    padding: 1.5px 5px;
    border-radius: 4px;
    background-color: rgba(255, 145, 0, 0.65);
    font-family: CenturyGothic;
    font-size: 12px;
    color: #fcfcfc;
    cursor: pointer;
}

.accumulate-icon {
    width: 83px;
    height: 83px;
    background-image: url('../../PageOptimization/static_svg/accumulate.svg');
    background-position: center;
    background-size: cover;
    margin-right: 16.5px;
}
